type IMapPayload = ICreateProposal.Actions.IMapPayload;

export const CreateProposalReducer = (state = INITIAL_STATE, action: IAction<IMapPayload>) => {
	switch (action.type) {
		case ActionConsts.CreateProposal.SetReducer:
			return {
				...state,
				...action.payload,
			};

		case ActionConsts.CreateProposal.SetNewNodeCounter: 
			return {
				...state,
				newNodeCounter: action.payload
			};
		

		case ActionConsts.CreateProposal.SetProposalInfo: 
			return {
				...state,
				proposalInfo: action.payload
			};

		case ActionConsts.CreateProposal.SetExclusionList:
			return {
				...state,
				exclusionList: action.payload
			};

		case ActionConsts.CreateProposal.SetBenefitNodes:
			return {
				...state,
				benefitNodes: action.payload
			};

		case ActionConsts.CreateProposal.SetContractType:
			return {
				...state,
				contractType: action.payload
			};

		case ActionConsts.CreateProposal.SetClientData:
			return {
				...state,
				clientData: action.payload
			};

		case ActionConsts.CreateProposal.SetSelectedPlanType:
			return {
				...state,
				selectedPlanType: action.payload
			};

		case ActionConsts.CreateProposal.SetSelectedPlanPackage:
			return {
				...state,
				selectedPlanPackage: action.payload
			};

		case ActionConsts.CreateProposal.SetSelectedBenefitCategory:
			return {
				...state,
				selectedBenefitCategory: action.payload
			};

		case ActionConsts.CreateProposal.SetEditedNodeList:
			return {
				...state,
				editedNodeList: action.payload
			};
			
		case ActionConsts.CreateProposal.ResetReducer:
			return INITIAL_STATE;

		default:
			return state;
	}
};
