export default function App() {
  const [selectedMenu, setSelectedMenu] = useState('');
  const onLogout = () => {
    window.location.replace('../index.html#/');
  };

  const RenderComponent = (selected: string, Component) => { 
    return class App extends React.Component { 
        componentDidMount() { 
          setSelectedMenu(selected);
        } 

        render() { 
           return <Component {...this.props} /> 
        }
    } 
  }
  
  let routes = [
    {
      path: '/client/:client_id',
      component: RenderComponent('clients', ClientProfilePage),
    },
    {
      path: '/client-benefits/:client_id/:contract_id',
      component: RenderComponent('clients', ClientBenefitsPage),
    },
    {
      path: '/create-proposal/:ticket_id',
      component: RenderComponent('proposals', CreateProposalPage),
    },
    {
      path: '/finalize-proposal/:client_id/:proposal_id/:user_type/:case_id',
      component: RenderComponent('proposals', FinalizeProposalPage),
    },
    {
      path: '/create-benefit',
      component: RenderComponent('proposals', CreateBenefit),
    },
    {
      path: '/approve-proposal/:client_id/:proposal_id/:ticket_id',
      component: RenderComponent('proposals', ApproveProposal),
    },
    {
      path: "/clients",
      component: RenderComponent('clients', ClientPage),
    },
    {
      path: "/proposals",
      component: RenderComponent('proposals', ProposalListPage),
    },
    {
      path: "/",
      component: RenderComponent('dashboard', Dashboard),
      exact: true,
    },
    {
      path: '/home',
      component: RenderComponent('dashboard', HomePage),
    },
    {
      path: '/login',
      component: LoginPage,
    },
    {
      path: '/renderer',
      component: RenderComponent('clients', RendererTest),
    },
    // {
    //   path: '/renderer',
    //   component: RendererTest,
    // }
  ];
  const prefix = '/underwriting';
  routes.map(route => (route.path = `${prefix}${route.path}`));


  return (
    <div style={{backgroundColor: 'white'}}>
      <ThemeProvider theme={theme}>
        <StylesProvider injectFirst>
          <MuiPickersUtilsProvider utils={MomentUtils}>
            <Helmet titleTemplate="NEO - %s" defaultTitle="NEO">
              <meta name="description" content="NEO application" />
            </Helmet>
            <CssBaseline>
              <Components.UI.PageLayout
                selectedMenu={selectedMenu}
                setSelectedMenu={setSelectedMenu}
                history={history}
                page={'underwriting'}
                onLogout={onLogout}
              >
                <Switch>
                  {renderRoutes(routes)}
                  <Route path="" component={NotFoundPage} />
                </Switch>
              </Components.UI.PageLayout>
            </CssBaseline>
          </MuiPickersUtilsProvider>
        </StylesProvider>
      </ThemeProvider>
    </div>
  );
}
