//#region Local Imports
import { ActionConsts } from './ActionConsts';
//#endregion Local Imports

//#region Interface Imports
import { IAction } from 'Components/Actions/IAction';
import { ICreateBenefitPage } from './ICreateBenefitPage';
//#endregion Interface Imports

/**
 * INITIAL_STATE
 */
const INITIAL_STATE: ICreateBenefitPage.IStateProps = {};

type IMapPayload = ICreateBenefitPage.Actions.IMapPayload;

export const CreateBenefitPageReducer = (state = INITIAL_STATE, action: IAction<IMapPayload>) => {
	switch (action.type) {
		case ActionConsts.CreateBenefitPage.SetReducer:
			return {
				...state,
				...action.payload,
			};

		case ActionConsts.CreateBenefitPage.ResetReducer:
			return INITIAL_STATE;

		default:
			return state;
	}
};
