type IMapPayload = IClientBenefitsPage.Actions.IMapPayload;

export const ClientBenefitsReducer = (state = INITIAL_STATE, action: IAction<IMapPayload>) => {
	switch (action.type) {
		case ActionConsts.ClientBenefits.SetReducer:
			return {
				...state,
				...action.payload,
			};

		case ActionConsts.ClientBenefits.SetBenefitNodes:
			return {
				...state,
				benefitNodes: action.payload
			};
			
		case ActionConsts.ClientBenefits.SetExclusionList:
			return {
				...state,
				exclusionList: action.payload
			};

		case ActionConsts.ClientBenefits.SetClientData:
			return {
				...state,
				clientData: action.payload
			};
			
		case ActionConsts.ClientBenefits.SetSelectedPlanType:
			return {
				...state,
				selectedPlanType: action.payload
			};

		case ActionConsts.ClientBenefits.SetSelectedPlanPackage:
			return {
				...state,
				selectedPlanPackage: action.payload
			};

		case ActionConsts.ClientBenefits.SetSelectedBenefitCategory:
			return {
				...state,
				selectedBenefitCategory: action.payload
			};


		default:
			return state;
	}
};
