/**
 * Asynchronously loads the component for ViewProposalPage
 */
import * as React from 'react';
import { connect } from 'react-redux';
import { Components, Utils } from '@hims/core';
const { loadable } = Utils;
const { LoadingIndicator } = Components.UI;

export default loadable(
  async () => {
    let { ProposalListPage/*, mapStateToProps, mapDispatchToProps*/ } = await import(
      './ProposalListPage'
    );
    const Component = connect(
      /*mapStateToProps,
      mapDispatchToProps,*/
    )(ProposalListPage);
    return { default: Component };
  },
  {
    fallback: <LoadingIndicator />,
  },
);
