type IMapPayload = IClientProfile.Actions.IMapPayload;

export const ClientProfileReducer = (state = INITIAL_STATE, action: IAction<IMapPayload>) => {
	switch (action.type) {
		case ActionConsts.ClientProfile.SetClientInfo:
			return {
				...state,
				clientInfo: action.payload
            };
		case ActionConsts.ClientProfile.SetProposalInfo:
				return {
					...state,
					proposalInfo: action.payload
				};
		case ActionConsts.ClientProfile.ResetReducer:
			return INITIAL_STATE;

		default:
			return state;
	}
};