/**
 * Asynchronously loads the component for CreateProposalPage
 */
import * as React from 'react';
import { connect } from 'react-redux';
import { Components, Utils } from '@hims/core';
const { loadable } = Utils;
const { LoadingIndicator } = Components.UI;

export default loadable(
  async () => {
    let { CreateProposalPage, mapStateToProps, mapDispatchToProps } = await import(
      './CreateProposalPage'
    );
    const Component = connect(
      mapStateToProps,
      mapDispatchToProps,
    )(CreateProposalPage);
    return { default: Component };
  },
  {
    fallback: <LoadingIndicator />,
  },
);
