/**
 * Asynchronously loads the component for ApproveProposalPage
 */
import * as React from 'react';
import { connect } from 'react-redux';
import { Components, Utils } from '@hims/core';
const { loadable } = Utils;
const { LoadingIndicator } = Components.UI;

export default loadable(
  async () => {
    let { ApproveProposalPage, mapStateToProps, mapDispatchToProps } = await import(
      './ApproveProposalPage'
    );
    const Component = connect(
      mapStateToProps,
      mapDispatchToProps,
    )(ApproveProposalPage);
    return { default: Component };
  },
  {
    fallback: <LoadingIndicator />,
  },
);
