import React from 'react';
import {
    Grid
} from '@material-ui/core';
import { RendererClass } from 'Pages/CreateProposal/Components/RendererClass';

interface RendererTestState {
    objId: string,
    renderType: string,
    fileType: string,
    apiContext: string
}

const inputStyle = {
    width: '100%',
    marginBottom: '1em',
    padding: '0.4em',
    borderRadius: '5px',
    border: '1px solid rgba(0, 0, 0, .4)'
}

const containerStyle = {
    paddingLeft: '2em'
}

export class RendererTest extends React.Component<{}, RendererTestState> {
    constructor(props) {
        super(props);

        this.state = {
            objId: '5da41b18f510d90b0051aae7',
            renderType: 'renderProposal',
            fileType: 'pdf',
            apiContext: 'base-benefit-plans'
        }
    }

    inputHandler = (e) => {
        let _input = e.target;

        switch (_input.name) {
            case 'objId':
                this.setState({
                    objId: _input.value
                });
                break;
            case 'renderType':
                this.setState({
                    renderType: _input.value
                });
                break;
            case 'fileType': 
                this.setState({
                    fileType: _input.value
                });
                break;
            case 'apiContext':
                this.setState({
                    apiContext: _input.value
                });
                break;
        }
    }

    render(): JSX.Element {
        const {
            objId,
            renderType,
            fileType,
            apiContext
        } = this.state;

        return (
            <Grid container style={containerStyle}>
                <Grid item sm={12}>
                    <input
                        style={inputStyle}
                        onChange={this.inputHandler}
                        type="text"
                        placeholder="objectId"
                        value={this.state.objId} 
                        name="objId" />
                </Grid>
                <Grid item sm={12}>
                    <select
                        style={inputStyle}
                        onChange={this.inputHandler}
                        name="renderType"
                        value={this.state.renderType}>
                        <option value="renderProposal">renderProposal</option>
                        <option value="renderContract">renderContract</option>
                    </select>
                </Grid>
                <Grid item sm={12}>
                    <select
                        style={inputStyle}
                        onChange={this.inputHandler}
                        name="fileType"
                        value={this.state.fileType}>
                        <option value="pdf">PDF</option>
                        <option value="docx">DOCX</option>
                    </select>
                </Grid>
                <Grid item sm={12}>
                    <input
                        style={inputStyle}
                        onChange={this.inputHandler}
                        type="text"
                        placeholder="API Context"
                        value={this.state.apiContext} 
                        name="apiContext" />
                </Grid>
                <Grid item sm={12}>
                    <RendererClass clientID="" clientName="" objId={objId} renderType={renderType} fileType={fileType} apiContext={apiContext}
                        onError={() => {
                            alert('error render');
                        }}
                        onStartDownload={() => {
                            console.log("sample render");
                        }} />
                </Grid>
            </Grid>
        )
    }
}