import { combineReducers } from 'redux';

import { CreateBenefitPageReducer } from 'Pages/CreateBenefitPage/CreateBenefitPageReducer';

import { Page2Reducer } from 'Pages/Page2/Page2Reducer';

import { HomeReducer } from 'Pages/Home/HomeReducer';

import { ApproveProposalReducer } from 'Pages/ApproveProposal/ApproveProposalReducer';

import { CreateProposalReducer } from 'Pages/CreateProposal/CreateProposalReducer';

import { FinalizeProposalReducer } from 'Pages/FinalizeProposal/FinalizeProposalReducer';

import { ClientProfileReducer } from 'Pages/ClientProfile/ClientProfileReducer';

import { ClientBenefitsReducer } from 'Pages/ClientBenefitsPage/ClientBenefitsReducer';

export default combineReducers({
    createBenefitPage: CreateBenefitPageReducer,
	page2: Page2Reducer,
	home: HomeReducer,
	createProposal: CreateProposalReducer,
	approveProposal: ApproveProposalReducer,
	finalizeProposal: FinalizeProposalReducer,
	clientProfile: ClientProfileReducer,
	clientBenefits: ClientBenefitsReducer,
});
